<template>
  <div class="wrapper">
    <div class="title">
      <h1>每日一句</h1>
      <div class="info">
        <label for="title">
          <span>作者：</span>
          <input id="author" v-model="author" type="text" placeholder="请输入作者" />
        </label>
      </div>
      <div class="info">
        <label for="vcode">
          <span>验证码：</span>
          <input id="vcode" v-model="vcode" type="text" placeholder="请输入验证码" />
        </label>
      </div>
      <div class="vcode">
        <label>
          <span></span>
          <img v-on:click="changeVCodeUrl" id="imgCaptcha" v-bind:src="vCodeUrl" alt />
        </label>
      </div>
      <div class="btn">
        <button v-on:click="submit()">提交</button>
      </div>
    </div>
    <div class="editor" ref="editor">
    </div>
  </div>
</template>

<script>
import E from 'wangeditor';
import { addEveryDay } from '../api/article';

export default {
  data() {
    return {
      editorContent: '',
      author: 'test',
      vCodeUrl: '/api/verificationCode',
      vcode: null,
    };
  },
  methods: {
    getContent() {
      return this.editorContent;
    },
    changeVCodeUrl() {
      this.vCodeUrl = `/api/verificationCode?${new Date().getTime()}`;
    },
    openMessage(messageType, message) {
      this.$message({
        message,
        type: messageType, // success/warning/info/error
      });
    },
    async submit() {
      // 验证属性
      if (!this.author) {
        this.openMessage('warning', '作者不能为空');
        return;
      }
      if (!this.editorContent) {
        this.openMessage('warning', '文章不能为空');
        return;
      }
      if (!this.vcode) {
        this.openMessage('warning', '验证码不能为空');
        return;
      }
      // 发送数据
      const data = {
        author: this.author,
        content: this.getContent(),
        verificationCode: this.vcode,
      };
      const result = await addEveryDay(data);
      // 返回消息
      if (+result.code === 0) {
        this.openMessage('success', result.msg);
        this.changeVCodeUrl();
        this.editor.txt.html(''); // 清空
        this.vcode = null;
        this.author = null;
      } else {
        this.openMessage('error', result.msg);
      }
    },
  },
  mounted() {
    this.editor = new E(this.$refs.editor);
    this.editor.customConfig.onchange = (html) => {
      this.editorContent = html;
    };
    this.editor.customConfig.uploadImgServer = '/api/upload';
    this.editor.customConfig.uploadFileName = 'img';
    this.editor.customConfig.uploadImgHooks = {
      customInsert(insertImg, result) {
        console.log(result);
        const url = result.data;
        insertImg(url);
      },
    };
    this.editor.create();
  },
};
</script>

<style lang="scss" scoped>
.wrapper {
  width: 100%;
}

.title {
  width: 800px;
  margin: 0 auto;
  h1 {
    height: 50px;
    line-height: 50px;
    font-size: 20px;
  }
  .info {
    height: 35px;
  }
  span {
    display: inline-block;
    width: 80px;
  }
  input {
    width: 300px;
  }
  .btn {
    button{
    margin-right: 5px;
    }
  }
}

.editor {
  width: 800px;
  height: 300px;
  margin: 0 auto;
}
</style>
